<template>
  <div class="header">
    <div class="menu-btn" @click="callMenu()">
      <img src="@/assets/images/icon-nav.svg" height="25" alt="">
    </div>
    <img src="@/assets/images/logo.png" height="25">
    <div class="user-btn" @click="callUser()">
      <img src="@/assets/images/icon-user.svg" height="25" alt="">
    </div>
    <div class="notice-btn">
      <img src="@/assets/images/icon-notice.svg" height="25" alt="">
    </div>
  </div>
</template>

<script>
export default {
  props: {
    menuOpenStatus: String,
    // user_status: String,
  },
  data(){
    return {
      menuStatus: this.menuOpenStatus,
      userStatus: this.user_status
    }
  },
  watch:{
    menuOpenStatus(){
      this.menuStatus =  this.menuOpenStatus
    }
	},
  methods:{
    callMenu(){
      if(this.menuStatus == 'active'){
        this.menuStatus = '';
      }else{
        this.menuStatus = 'active';
      }
      this.$emit('menuStatus', this.menuStatus)
    },
    
    callUser(){
      if(this.userStatus == 'active'){
        this.userStatus = '';
      }else{
        this.userStatus = 'active';
      }
      this.$emit('userStatus', this.userStatus)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .header{
    position: relative;
    width: 100%;
    height: 50px;
    /* background: #212335; */
    background: #181a2b;
    padding: 0;
    line-height: 50px;
    color: #fff;
    text-align: center;
  }

  .menu-btn{
    position: relative;
    float: left;
    width: 50px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    cursor: pointer;
    transition: 0.2s all;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
  }
  .notice-btn,
  .user-btn{
    position: relative;
    float: right;
    width: 50px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    cursor: pointer;
    transition: 0.2s all;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
  }

  .menu-btn:hover,
  .user-btn:hover,
  .notice-btn:hover{
    background: #4e505f;
  }

</style>
