<template>
  <div class="container-fluid">
    <div class="row pb-3">
      <div class="col-12 mt-3">
        <h2>Customer Profile</h2>
      </div>
      
      <div class="col-12 col-sm-12 col-md-6 col-lg-6 col-xl-4">
        <div class="box">
          <div class="content">
            <canvas id="chartCustomerProfile"></canvas>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Chart from 'chart.js'

var _chart01;

export default {
  props: {
  },
  mounted:function(){ 
    this.init() 
  },
  data(){
    return {
      chart01Data : [
        ['APEC','EU','NALA','中嘉'],
        ['20.02','33.89','21.2','24.89']
      ],
    }
  },
  methods:{
    init(){
      this.setChart();
    },
    setChart(){
      if(_chart01){
        _chart01.destroy();
      }
      
      var ctx01 = document.getElementById('chartCustomerProfile');
      _chart01 = new Chart(ctx01, {
          type: 'doughnut',
          data: {
              labels: this.chart01Data[0],
              datasets: [{
                  label: 'SellinQty',
                  data: this.chart01Data[1],
                  backgroundColor: [
                      '#ff6384'
                      ,'#ffcd56'
                      ,'#36a2eb'
                      ,'#66c9c9'
                  ],
                  borderColor: [
                      '#ff6384'
                      ,'#ffcd56'
                      ,'#36a2eb'
                      ,'#66c9c9'
                  ],
                  borderWidth: 0,
              }],
          },
          options: {
            aspectRatio: 2,
            // cutoutPercentage: 50
            legend: {
              labels: {
                  width: 50,
                  fontColor: '#fff',
                  fontFamily: 'Microsoft JhengHei'
              },
              position : "top"
            },
            // title: {
            //     display: false,
            //     text: 'Custom Chart Title'
            // },
            // layout: {
            //     padding: {
            //         left: 0,
            //         right: 0,
            //         top: 0,
            //         bottom: 0
            //     }
            // }
          }
      });
    }
  }
}
</script>

