<template>
  <div class="container-fluid">
    <div class="row pb-3">
      <div class="col-12 mt-3">
        <h2>Sales Insight</h2>
      </div>
      <div class="col-12">
        <div class="row">
          <div class="col-6 col-md-3">
            <div class="info-card card-color-01">
              <div  >
                <img src="@/assets/images/icon-chart-1.svg" height="25" alt="">
                本月銷售額
              </div>
              <div>
                {{allData[0]}}
              </div>
            </div>
          </div>
          <div class="col-6 col-md-3">
            <div class="info-card card-color-02">
              <div>
                <img src="@/assets/images/icon-shopping-cart.svg" height="25" alt="">
                本月銷售量
              </div>
              <div>
                {{allData[1]}}
              </div>
            </div>
          </div>
          <div class="col-6 col-md-3">
            <div class="info-card card-color-03">
              <div>
                <img src="@/assets/images/icon-money.svg" height="25" alt="">
                本季銷售額
              </div>
              <div>
                {{allData[2]}}
              </div>
            </div>
          </div>
          <div class="col-6 col-md-3">
            <div class="info-card card-color-04">
              <div>
                <img src="@/assets/images/icon-chart-2.svg" height="25" alt="">
                今年銷售額
              </div>
              <div>
                {{allData[3]}}
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-12 col-sm-12 col-md-12 col-lg-6 col-xl-4">
        <div class="box">
          <div class="title">
            Rev Target Late
          </div>
          <div class="content">
            <canvas id="ChartRevTargetLate"></canvas>
          </div>
        </div>
      </div>
      <div class="col-12 col-sm-12 col-md-6 col-lg-6 col-xl-4">
        <div class="box">
          <div class="title">
            多產品全球實銷佔比
          </div>
          <div class="content">
            <canvas id="myChart01"></canvas>
          </div>
        </div>
      </div>
      <div class="col-12 col-sm-12 col-md-6 col-lg-6 col-xl-4">
        <div class="box">
          <div class="title">
            區域全球實銷佔比
          </div>
          <div class="content">
            <canvas id="myChart02"></canvas>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Chart from 'chart.js'

var _chart01;
var _chart02;
var _chart03;

export default {
  props: {
    menustatus: String
  },
  mounted:function(){
    this.init() 
  },
  data(){
    return {
      allData : ['NT 1.05B','NTD 372.44K','NTD 9.07B','NTD 44.5B'],
      chart01Data : [
        ['VGA','MB','Mointer','Brix','SSD'],
        [50.07, 44.51,1.34,1.88,2.2]
      ],
      chart02Data : [
        ['APEC','EU','NALA','中嘉'],
        ['20.02','33.89','21.2','24.89']
      ],
      chart03Data : [
        ['APEC','EU','NALA','中嘉'],
        [['33.89','21.2','20.02','24.89'],[10,3,7,15]]
      ]
    }
  },
  methods:{
    init(){
      this.setChart();
    },
    setChart(){
      if(_chart01){
        _chart01.destroy();
      }
      if(_chart02){
        _chart02.destroy();
      }
      if(_chart03){
        _chart03.destroy();
      }
      var ctx01 = document.getElementById('myChart01');
      var ctx02 = document.getElementById('myChart02');
      var ctx03 = document.getElementById('ChartRevTargetLate');
      
      _chart01 = new Chart(ctx01, {
          type: 'doughnut',
          data: {
              labels: this.chart01Data[0],
              datasets: [{
                  label: 'SellinQty',
                  data: this.chart01Data[1],
                  backgroundColor: [
                      '#ff6384'
                      ,'#ffcd56'
                      ,'#36a2eb'
                      ,'#66c9c9'
                      ,'#a87dff'
                  ],
                  borderColor: [
                      '#ff6384'
                      ,'#ffcd56'
                      ,'#36a2eb'
                      ,'#66c9c9'
                      ,'#a87dff'
                  ],
                  borderWidth: 0,
              }],
          },
          options: {
            aspectRatio: 2,
            // cutoutPercentage: 50
            
            legend: {
              labels: {
                  width: 50,
                  fontColor: '#fff',
                  fontFamily: 'Microsoft JhengHei'
              },
              position : "left"
            }
          }
      });

      _chart02 = new Chart(ctx02, {
          type: 'doughnut',
          data: {
              labels: this.chart02Data[0],
              datasets: [{
                  label: 'SellinQty',
                  data: this.chart02Data[1],
                  backgroundColor: [
                      '#ff6384'
                      ,'#ffcd56'
                      ,'#36a2eb'
                      ,'#66c9c9'
                  ],
                  borderColor: [
                      '#ff6384'
                      ,'#ffcd56'
                      ,'#36a2eb'
                      ,'#66c9c9'
                  ],
                  borderWidth: 0,
              }],
          },
          options: {
            aspectRatio: 2,
            // cutoutPercentage: 50
            legend: {
              labels: {
                  width: 50,
                  fontColor: '#fff',
                  fontFamily: 'Microsoft JhengHei'
              },
              position : "left"
            },
            // title: {
            //     display: false,
            //     text: 'Custom Chart Title'
            // },
            // layout: {
            //     padding: {
            //         left: 0,
            //         right: 0,
            //         top: 0,
            //         bottom: 0
            //     }
            // }
          }
      });

      _chart03 = new Chart(ctx03, {
          // type: 'bar',
          type: 'horizontalBar',
          data: {
              labels: this.chart03Data[0],
              datasets: [{     
                  // categoryPercentage: 1,
                  // barPercentage: 0.5,
                  // barThickness: 20,
                  label: '本季銷售額',
                  data: this.chart03Data[1][0],
                  backgroundColor: '#ff6384',
                  borderColor: '#ff6384',
                  borderWidth: 0,
                  order: 1
              },{     
                  // categoryPercentage: 1,
                  // barPercentage: 0.5,
                  // barThickness: 20,
                  label: '本月銷售量',
                  data: this.chart03Data[1][1],
                  backgroundColor: 'rgba(255,255,255,0.2)',
                  borderColor: '#ffcd56',
                  borderWidth: 2,
                  order: 2,
                  // type: 'line',
              }
              ],
          },
          options: {
            // cutoutPercentage: 50,
            responsive: true,
            maintainAspectRatio: true,
            legend: {
              labels: {
                  width: 50,
                  fontColor: '#fff',
                  fontFamily: 'Microsoft JhengHei'
              },
              position : "top"
            },
            scales: {
                xAxes: [{
                    ticks: {
                        beginAtZero:true,
                        fontColor: '#fff'
                    },
                }],
                
                yAxes: [{
                    ticks: {
                        beginAtZero:true,
                        fontColor: '#fff'
                    },
                }]
            }
            // title: {
            //     display: false,
            //     text: 'Custom Chart Title'
            // },
            // layout: {
            //     padding: {
            //         left: 0,
            //         right: 0,
            //         top: 0,
            //         bottom: 0
            //     }
            // }
          }
      });
    }
  }
}
</script>

